<template>
  <div>
    <div class="login-header">
      <div class="header-warp">
        <span style="cursor: pointer;" @click="$router.push('/')"> 政企智慧服务平台</span><span >忘记密码</span>
      </div>

    </div>
    <div class="fg-body">
      <div class="loginForm" style="position: relative">
        <div class="fg-head" >
          忘记密码
        </div>
        <div class="loginL">
          <ul>
            <li> <span style="color: #E84343">*</span> 登陆账号&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 验证码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 邮箱验证码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 新密码&nbsp;&nbsp;:&nbsp;</li>
            <li> <span style="color: #E84343">*</span> 确认新密码&nbsp;&nbsp;:&nbsp;</li>
          </ul>
        </div>
        <div class="loginR">
          <ul >
            <li>
              <el-input v-model="account" placeholder="请输入用户名或邮箱号" style="padding-left: 5px;width: 127%"></el-input>
<!--              <input type="text" placeholder="请输入用户名或手机号" style="padding-left: 5px" v-model="account">-->
            </li>
            <li class="verify">
              <el-input v-model="verify"  style="padding-left: 5px; width: 92%" ></el-input>
              <img src="" alt="" @click="toggleVerify" ref="verify">
              <span @click="toggleVerify">看不清楚？点击刷新</span>
            </li>
            <li class="phoneVerify">
              <el-input v-model="mailVerify"  style="padding-left: 5px; width: 92%" ></el-input>
              <button @click="getMailVerify">获取验证码</button>
            </li>
            <li>
              <el-input v-model="password" placeholder="8-20位字符" style="padding-left: 5px;width: 127%" show-password></el-input>
            </li>
            <li>
              <el-input v-model="repassword" placeholder="8-20位字符" style="padding-left: 5px;width: 127%" show-password></el-input>
            </li>
            <li class="treaty">
              <el-checkbox v-model="checked">我已阅读并同意《安徽政务服务网个人用户注册协议》</el-checkbox>
            </li>
            <li class="register">
              <button style="cursor:pointer;" @click="complete">修改</button>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="footer">
      <div>版权所有:淮北市经信局</div>
      <div>网站备案号：皖ICP备10207486号 　网站标识码：3708000041</div>
      <div style="padding-bottom: 38px">党政机关网站标识号：CA162410000604238070001        皖公网安备 37089702000262号</div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "forgetPW",
        data(){
            return{
                account:'',
                verify:'',
                phoneVerify:'',
                password:'',
                repassword:'',
                mailVerify:'',
                checked:false
            }
        },
        mounted(){
            this.toggleVerify()
        },
        methods:{
            //获取邮箱验证码
            getMailVerify(){
                // 检验验证码
                var that = this;
                var email = that.account;
                var mmCode = that.verify;
                if (that.account == ''){
                    this.$message({
                        message: '请输入账号',
                        type: 'warning'
                    });
                    return false
                }
                if (that.verify == ''){
                    this.$message({
                        message: '请输入验证码',
                        type: 'warning'
                    });
                    return false
                }
                if (that.verify.length != 4){
                    this.$message({
                        message: '请输入验证码',
                        type: 'warning'
                    });
                    return false
                }
                this.$axios.get('/verificationCode?code='+that.verify).then(function(res) {
                    //邮箱验证码
                    if (res.data.code==0){
                        that.$axios.post('/getEmailCode', that.$qs.stringify({
                            userName:email,
                        })).then(function(res) {
                            if (res.data.code ==0){
                                that.$message({
                                    message: '验证码已发送到邮箱!',
                                    type: 'success'
                                });
                            }else {
                                console.log(res);
                                that.$message.error(res.data.msg);
                            }
                        })
                    }else {
                        that.$message.error(res.data.msg);
                    }
                })
            },
            togglePassword(){
                  var password = this.$refs.password;
                  console.log(password)
            },
            //切换验证码
            toggleVerify(){
                var that = this;
                var img = this.$refs.verify;
                img.src= 'http://192.168.20.24:8088/getCode?' +Math.random();
            },
           //完成修改密码
            complete(){
                var that = this;
                if (that.account == ''){
                    this.$message({
                        message: '请输入账号',
                        type: 'warning'
                    });
                    return false
                }
                if (that.verify == ''){
                    this.$message({
                        message: '请输入验证码',
                        type: 'warning'
                    });
                    return false
                }

                that.$axios.post('/user/retrievePassword', that.$qs.stringify({
                    mmCode: that.mailVerify,
                    userName: that.account ,
                    newPassword:that.password
                })).then(function(res) {
                    if (res.data.code==0){
                        that.$message({
                            message: '密码已修改成功！',
                            type: 'success'
                        });
                        that.$router.push('/login')
                    }else{
                        console.log(res.data.msg);
                        that.$message.error(res.data.msg);
                    }
                })
            }
        }
    }
</script>

